import React, { HTMLAttributes } from 'react';

import { Button } from '../Button';
import { Container } from '../Container';
import { SvgArrowRight } from '../Icons';
import { StackModule } from '../Stack';
import { Text } from '../Typography';

import styles from './Hero.module.scss';

export interface HeroProps
    extends Omit<HTMLAttributes<HTMLElement>, 'className'> {
    imageUrl: string;
    heading: string;
    description: string;
    buttonText: string;
    buttonHandler?: () => void;
}

export const Hero: React.FC<HeroProps> = ({
    imageUrl,
    heading,
    description,
    buttonText,
    buttonHandler,
    ...props
}) => {
    // Remove unnecessary props
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const { className, ...sectionProps } = props as HTMLAttributes<HTMLElement>;

    return (
        <section
            className={styles.base}
            style={{ backgroundImage: `url(${imageUrl})` }}
            {...sectionProps}
        >
            <div className={styles.mask}>
                <Container>
                    <div className={styles.inner}>
                        <StackModule gap={{ xsmall: 2 }}>
                            <Text.Heading variant="h1" className={styles.title}>
                                {heading}
                            </Text.Heading>
                            <Text.Body className={styles.description}>
                                {description}
                            </Text.Body>
                            <Button
                                onPress={buttonHandler}
                                iconRight={<SvgArrowRight />}
                                className={styles.button}
                            >
                                {buttonText}
                            </Button>
                        </StackModule>
                    </div>
                </Container>
            </div>
        </section>
    );
};

export default Hero;
